.searchResult{
    display: flex;
    position: relative;
    margin: 20px;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    cursor: pointer;
    align-items: center;
}

.searchResult:hover{
    opacity: 0.8;
}

.searchResult>img{
    object-fit: fill;
    height: 200px;
    width: 350px;
    border-radius: 10px;
    overflow: hidden;
}

.searchResult__info{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 50px;
}

.searchResult__heart{
    position: absolute;
    top: 20px;
    right: 40px;
}

.searchResult__infoTop{
    width: 40vw;
}

.searchResult__infoTop>h3{
    font-weight: 300;
    margin-top: 10px;
}

.searchResult__infoTop>p{
    margin-top: 10px;
    font-size: 13px;
    color: gray;
}

.searchResult__infoBottom{
    display: flex;
    justify-content: space-between;
}


.searchResult__star{
    color: red;
}

.searchResult__stars{
    display: flex;
    align-items: center;
}

.searchResult__price>p{
    text-align: right;
}

.searchResult__price{
    position: absolute;
    bottom: 20px;
    right: 30px;
}

@media screen and (max-width: 760px){
    .searchResult__price{
        position:static;
    }

}

@media screen and (max-width: 700px){
    .searchResult__info{
        padding-left: 20px;
    }
    .searchResult__price{
        position:static;
    }

    .searchResult__infoBottom{
        flex-direction: column;
    }
    .searchResult__price>p{
        text-align: left;
    }

    .searchResult>img{
        transform: scale(0.9);
    }
    

}

@media screen and (max-width: 600px){
    .searchResult{
        flex-direction: column;
        margin: 0;
    }
    .searchResult>img{
        transform: scale(0.85);
    }
    .searchResult__info{
    
    }
    .searchResult__heart{
        position: absolute;
        top: 222px;
        right: 0px;
    }
    .searchResult__infoTop{
        width: 70vw;
    }
    
    

}

